import React, { Component } from 'react';
import logo_bw from './../images/logo_bw.png';
import Description from './description.js';
import { text,locale } from './../util/i18n.js';
const styles ={
	
	featureCard :{
				
				textShadow: '1px 1px 1px #fff',
				fontFamily:'sans-serif',
				//color:'#333',
				fontSize:'16px',			
				//float: 'left',
				lineHeight: 1.8,		
				textAlign: 'left',
				
				
			},
			
			cardDiv : 'w3-round-xxlarge',
			
		
			
			vertiStyle :{
				letterSpacing:'1.3px',
				fontSize:'14px',
			},
			subtext:{
				fontSize:'14px',
				opacity:0.7,
				marginLeft:'25px',
				marginTop: '-1em',
			}
	
	}

const cards = [];
const lbtns = [];
const items = ['overview',
		'ultrasimple', 
		'livedemo',
		'onemonthfree',
		'payasyougain',
		'roboexpertbacking',
		'backtested',
		'hybrid',
		'cloudbased',
		'tunable',
		'saftywithtransparency',
		'modeltrader'  ];

		
	

let pageno = 0;

export default class Features extends Component{
	
	constructor( props )
   {
       super(props);
	   this.state ={ cards : 12,pagenm : 0,language:null };
	   //this.loadCards();
   }
   
   render(){
	   if(this.state.language != locale() ){
		   // alert(this.state.language);
			this.loadCards();
		   this.setState({language:locale()});
	   }
	  
	   return(
			
			<div className="w3-cell-row w3-border-bottom ">
				<div className='w3-cell w3-hide-small w3-hide-medium ' style = { { width : '15%' } } >	
				
					{ 
						Object.entries( this.btns ).map( ( [ key, value ] ) =>{								
							
							return this.lbtn( key, value, this.state.pagenm );
							}
						)
					}					
				
				</div>
				
				<div className='w3-cell w3-display-container ' style={{paddingLeft:'5%', paddingRight:'5%',}}   >
					
					<div className ="w3-container w3-cell-bottom w3-border-top w3-border-black " />					
					{ this.cardList( this.state.pagenm, this.props.nmCards ) }					
								
				<div className ="w3-container w3-cell-bottom w3-large w3-padding" />	
				<div className='w3-display-bottommiddle w3-border-top w3-border-black' style={{fontSize:'12px', width:'85%'}}>	
					
					<button className = "w3-button  w3-left " onClick = { this.prev.bind( this ) }>
						<i className="fa fa-arrow-circle-left fa-2x" aria-hidden="true"></i>
					</button>			
					
					<button className = "w3-button w3-right" onClick = { this.next.bind( this ) } >
						 <i className="fa fa-arrow-circle-right fa-2x" aria-hidden="true"></i>
					</button>	
			
				</div>
				</div>
				
				<div className='w3-cell w3-hide-small w3-hide-medium' style={{width:'15%'}}>
				
					{ 
						Object.entries(this.btns).map(([key,value])=>{
								return this.rbtn( key, value, this.state.pagenm );
							}
						)
					}
				
				</div>
			</div>  
	   );
   }
	   
	   componentDidMount(){
		  // this.loadButtons();
		  
	   }
	btns = [];  
   
   lbtn = function( idx, lable, pagenm ){
	   
	   if( idx > 5 ) return ;
	   let clz = 'w3-button w3-block    w3-left-align w3-text-gray' ;  
	   
	   
	   if( idx == 0  ) {
		   clz = ' w3-border-top ' + clz;
	   }
	   if( idx == pagenm  ) {
		   clz = ' w3-border-black w3-border-bottom w3-border-top ' + clz;
	   }
	   else clz = clz + ' w3-border-bottom';
	  
		return (
		   <div key={'ftr' +idx }
				className={clz} 			
				style={styles.vertiStyle} 
				onClick={() => { this.jmp(idx).bind(this)} }>{lable}
			</div> );
	 
	   
	  
   }
   
   rbtn = function( idx, lable, pagenm ){
	   
	   if( idx < 6 ) return ;
	   let clz = 'w3-button w3-block   w3-text-gray  w3-right-align';
	  
	   
	   
	  if( idx == 6  ) {
		   clz = ' w3-border-top ' + clz;
	   }
	   if( idx == pagenm  ) {
		   clz = ' w3-border-black w3-border-bottom w3-border-top ' + clz;
	   }
	   else clz = clz + ' w3-border-bottom ';
	  
		return (
			<div 
				key={'b'+idx}
				className={clz} 			
				style={styles.vertiStyle} 
				onClick={() => { this.jmp(idx).bind(this)} }>{lable}
			</div> );   
	  
		}


   
   cardList = function( pagenm, nmCards ){
	   
	   let items = [];
	
	   for (let index = pagenm ; index < (pagenm +nmCards) ; index++ ) {
		    
			items.push( cards [index]);
		}
		
		return (
			<div className=' w3-center w3-round-large w3-margin-bottom' 
			style={{display: 'flex', justifyContent:'space-around', }} >
			{items}
				
			</div>
		);
	   
   }
   next = function(){
		
		let vlu = this.state.pagenm+this.props.nmCards;
		if( vlu >= this.state.cards )vlu = 0;
	
		this.setState({pagenm:vlu});
	}
	prev = function(){
		
		let vlu = this.state.pagenm-this.props.nmCards;
		if( vlu < 0 )vlu = 0;
		this.setState({pagenm:vlu});
	}
	jmp = function( idx ){

		this.setState({pagenm:parseInt(idx)});
	}
	

	loadCards = function(){
		this.btns = text('featuremenulabels');
	/*	text('overview_m'),
		text('ultrasimple_m'), 
		text('livedemo_m'),
		text('onemonthfree_m'),
		text('payasyougain_m'),
		text('roboexpertbacking_m'),
		text('backtested_m'),
		text('hybrid_m'),
		text('cloudbased_m'),
		text('tunable_m'),
		text('saftywithtransparency_m'),
		text('modeltrader_m')];  */
		cards =[];
		let item = null;
		for( var ctr =0; ctr < items.length;ctr++){
			item = items[ctr];
			cards.push(
					
					<div className={styles.cardDiv} style={styles.featureCard} key='c1'>	
						
						<p  style={{fontSize:'16px',  color:'#000'}}>
						<b>{this.btns[ctr]}</b> :&nbsp;
							{text(item)}
						</p>	
			</div>
		
			);
		}
		/*
		cards.push(
					
					<div className={styles.cardDiv} style={styles.featureCard} key='c1'>	
								
				<p  style={{fontSize:'16px',  color:'#000'}}>
					{text('overview')}
				</p>	
			</div>
		
			);
	   
	  cards.push(
					<div className={styles.cardDiv} style={styles.featureCard} key='c2'>
				
						
						<p style={{  color:'#000'}}>
							<b>{text('ultrasimple_m')} : </b>
							{text('ultrasimple')}
						</p>
					</div>
				);
			
				cards.push(
					<div className={styles.cardDiv} style={styles.featureCard} key='c3'>	
						
						
						<p style={{   color:'#000'}}>
							<b>{text('livedemo_m')} : </b>
							{text('livedemo')}
							
						</p>	
						<p style={styles.subtext} className='w3-hide'>
							Click on <a href='#' >Login</a> from the menu. Follow simple steps to register for Live Demo by selecting <b><i>Prameyaka</i></b> as your broker.
						</p>
						
						
						
					</div>
						 
				);
			
				cards.push(
					 
					<div className={styles.cardDiv} style={styles.featureCard} key='c4'>	
						
						<p  style={{   color:'#000'}}>
						<b>{text('onemonthfree_m')}&nbsp;:&nbsp;</b>{text('onemonthfree')}
						</p>
						
					</div>
				);
			
				cards.push(
					<div className={styles.cardDiv} style={styles.featureCard} key='c5'>	
						
						<p  style={{   color:'#000'}}>
						<b>{text('payasyougain_m')}&nbsp;:&nbsp;</b>{text('payasyougain')}
						</p>
					</div>	
				);
				
					cards.push(
						<div className={styles.cardDiv} style={styles.featureCard} key='c6'>	
							
							<p style={{   color:'#000'}}>					
								
								<b>{text('roboexpertbacking_m')}&nbsp;:&nbsp;</b>{text('roboexpertbacking')}
								</p>
						</div>
					);
				
					cards.push(
						<div className={styles.cardDiv} style={styles.featureCard} key='c7'>	
										 
							 <p className=" w3-center " style={{ fontSize:'14px'}}>
								 <i>
								{text('backtested_q')}
								 </i>
							</p>
							<p  style={{   color:'#000'}}>	
					
								<b>{text('backtested_m')}&nbsp;:&nbsp;</b>
								{text('backtested')}
								
							</p>
						</div>
					);
				
					cards.push(
						<div className={styles.cardDiv} style={styles.featureCard} key='c8'>	
											 
							<p style={{   color:'#000'}}>	 
					
								<b>{text('hybrid_m')}&nbsp;:&nbsp;</b>
								{text('hybrid')}
							</p>
						</div>
					);
				
					cards.push(
						<div className={styles.cardDiv} style={styles.featureCard} key='c9'>	
										 
							<p style={{ color:'#000'}} >	
								<b>{text('cloudbased_m')}&nbsp;:&nbsp;</b>
								{text('cloudbased')} 
								</p>
											
						</div>
					);
				
					cards.push(
						<div className={styles.cardDiv} style={styles.featureCard} key='c10'>	
										 
							<p style={{ color:'#000'}}>	
					
								<b>{text('tunable_m')}&nbsp;:&nbsp;</b>
								{text('tunable')} 
											
							</p>
						</div>
				
		
				);
			cards.push(			
			
				<div className={styles.cardDiv} style={styles.featureCard} key='c11'>
					
					<p style={{ color:'#000'}}>	
							
						<b>SAFTY WITH TRANSPERANCY : </b>Prameyaka can not do any money transaction, but can only trigger trades on behalf of user. Any time user can view the real-time details of their fund and trades triggered by Prameyaka.
					</p>
					
				</div>
			);
			cards.push(
				<div className={styles.cardDiv} style={styles.featureCard} key='c12'>
					
	
					<p style={{ color:'#000'}}>	
						<b>MODEL TRADER : </b>Prameyaka has a unique concept called Model trader with Rs. 1 lacks of fund allocation. All trades are triggered with platform's settings with user end tuners disabled. 
						Model trader's data will be captured and taken as a reference to tune the algorithms and trading strategies.<br/> Each day, we will publish actual return earned by the Model Trader.				
					</p>
				</div>	   
			);
			*/
	}
  
}


